.backdrop{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(32, 13, 29, 0.6);
    opacity: 1;
    overflow-y: scroll;
}
 
.modal{
    background-color: $subtitle-text-color;
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%,-50%) scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    @media screen and (max-width: $max-mobile){
        width: 272px;
        height: 915px;
        padding: 75px 12px;
        border-radius: 150px 0px;

    }
    @media screen and (min-width: $tablet){
        width: 595px;
        height: 831px;
        padding: 28px 17px 56px 50px;
        border-radius: 300px 0px;

    }

    @media screen and (min-width: $desktop) {
        width: 942px;
        height: 1023px;
        padding: 62px 30px;
        border-radius: 300px 0px;

    }
    .backdrop.is-hidden &{
        transform: translate(-50%,-50%) scale(0.8);
    }
}
.circle-plug {
    // удалить потом когда будут фотки
    width: 132px;
    height: 132px;
    border-radius: 50%;
    background-color: red;
    margin: auto;
}

.foto-info{
    display: flex;
}
.modal .close-window{
    position: absolute;
    right: 25px;
    border-radius: 50%;
    cursor: pointer;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0);
    @media screen and (max-width: $max-mobile){
        top: 25px;
    }

    }
.modal .close-icon{
    @media screen and (max-width: $max-tablet){
        width: 17px;
        height: 17px;
    }
}
